<template>
  <div class="recharge-page">
    <van-nav-bar
      fixed
      title="Recharge"
      left-text="Back"
      left-arrow
      @click-left="onClickLeft"
    />
   <!-- <div class="balance-wrap">
     <div class="title">Balance:</div>
     <div class="value-wrap">
       <div class="value">{{ profile.diamond }}</div>
       <div class="tips">Diamonds</div>
     </div>
   </div> -->
   <div class="content-block available-coins">
     <h1>Available Coins</h1>
     <div class="balance-container">
       <img class="star-icon" :src="require('@/assets/icon/star-icon.png')" />
       <span class="balance">{{ profile.diamond}}</span>
     </div>
   </div>
   <!--  -->
   <div class="content-block choose-your-plan">
     <h1>Choose Your plan</h1>
   </div>
   <!--  -->
   <div class="content-block diamond-list">
     <div class="diamond-item"
      @click="diamondHandler(_)"
      :class="{ hot: _.type === 1 }"
      v-for="(_, i) in products"
      :key="i"
     >
       <div>
         <div class="diamond-preview">
            <img class="diamond-icon" :src="require('@/assets/icon/star-icon.png')" /> <span class="diamond-count">{{ _.diamond }}</span>
          </div>
          <p class="bonus-diamond">bonus <span>{{ _.diffDiamond }}</span> diamond</p>
       </div>
       <div class="recharge-button">
         <span class="diamond-money">${{ _.money }}</span>
         <img :src="require('@/assets/icon/right-icon.png')" />
       </div>
     </div>
   </div>
    <!-- <div class="title-tips">Recharge Plan</div>
    <div class="recharge-list">
     <div class="list-title">Minute basis:</div>
      <div class="list-items">
        <div
          class="item"
          @click="diamondHandler(_)"
          :class="{ hot: _.type === 1 }"
          v-for="(_, i) in products"
          :key="i"
        >
          <div class="label">
            <van-icon :name="require('@/assets/icon/star-icon.png')" />
            <div class="hot-label">{{ _.preDiamond+_.diffDiamond }}</div>
            <div>{{ _.diamond }}</div>
            <div class="hot-tag">HOT</div>
          </div>
          <div class="sale-money">{{ _.money }} USD</div>
        </div>
      </div>
    </div> -->
    <van-dialog
      v-model="dialog"
      @opened="renderCard"
      @confirm="purchase"
      title="Recharge"
      show-cancel-button
    >
      <div ref="card"></div>
    </van-dialog>
  </div>
</template>

<script>
import Vue from "vue";
import { NavBar, Dialog } from "vant";
import * as Server from "@/server";
import { mapState } from "vuex";
let stripe = window.Stripe("pk_test_kKiIIn5jbIixQ96SV4NpPZyf00hulVQYuC"),
  elements = stripe.elements(),
  card = undefined;
const styles = {
  base: {
    border: "1px solid #d8d8d8"
  }
};

Vue.use(NavBar).use(Dialog);
export default {
  data() {
    return {
      dialog: false,
      products: [],
      curProduct: null
    };
  },
  computed: {
    ...mapState(["profile"])
  },
  created() {
    if (this.$route.query.token) {
      localStorage.setItem("token", this.$route.query.token);
    }
  },
  methods: {
    async loadData() {
      const { data, status } = await Server.ProductList();
      if (status === 0) {
        this.products = data;
      }
    },
    async diamondHandler(item) {
      console.log("Diamond handler > ", item);
      let {data, status} = await Server.PayByPaytm(item.productIosId);
      if(status === 0){
        window.open(data.payUrl);
      }
      console.log("Diamond Handler => ", data, status);
      // this.curProduct = item;
      // this.dialog = true;
    },
    renderCard() {
      card.mount(this.$refs.card);
    },
    purchase() {
      stripe.createToken(card).then(async result => {
        // Access the token with result.token
        // console.log(result);
        if (result.error) {
          this.$toast(result.error.message);
        } else {
          console.log(result);
          const { data, status } = Server.PayByStripe({
            code: result.token.id,
            diamond: this.curProduct.diamond,
            money: this.curProduct.money,
            productId: this.curProduct.productId,
            type: this.curProduct.type
          });
          console.log(status, data);
          //   Send the token to your server.
          //    stripeTokenHandler(result.token);
        }
      });
    },
    onClickLeft() {
      this.$router.go(-1)
    }
  },
  mounted() {
    this.loadData();
    card = elements.create("card", styles);
  }
};
</script>

<style lang="less">
.van-dialog {
  width: 90%;
}
.content-block {
  padding: 20px;
  border-radius: 10px;
}
.content-block + .content-block {
  margin-top: 5px;
}
.available-coins, .choose-your-plan {
  text-align: center;
  background: linear-gradient(to top right, rgb(255,0,122), rgb(255, 1, 47), rgb(255,0,122));
  color: #FFF;

  h1{
    font-size: 20px;
    margin: 0;
  }

  h1 + * {
    margin-top: 15px;
  }

  .balance-container {
    display: flex;
    align-items: center;
    justify-content: center;
    
    .star-icon {
      max-height: 20px;
      margin-right: 5px;
    }

    .balance {
      font-size: 20px;
      font-weight: bold;
      color: #FFF;
    }
  }

}

.diamond-list {
  background: rgb(255, 230, 234);
  
  .diamond-item {
    display: flex;
    align-items: flex-end;
    justify-content: space-between;
  }
  .diamond-item + .diamond-item {
    margin-top: 30px;
  }

  .diamond-preview {
    height: 20px;
    display: flex;
    align-items: flex-end;
  }
  .diamond-icon {
    max-height: 20px;
    margin-right: 5px;
  }
  .diamond-count {
    font-size: 18px;
    line-height: 20px;
    color: #000;
  }
  
  .diamond-money {
    color: #000;
    font-size: 18px;
    line-height: 20px;
  }

  .bonus-diamond {
    font-size: 14px;
    margin: 10px 0 0 0;

    span {
      color: #F00;
    }
  }

  .recharge-button {
    background: linear-gradient(to right, rgb(255,0,122), rgb(255, 1, 47));
    font-size: 16px;
    color: #FFF;
    display: flex;
    align-items: center;
    justify-content: center;
    height: 30px;
    padding: 0 10px 0 20px;
    border-radius: 20px;

    .diamond-money {
      color: #FFF;
      font-size: 14px;
    }

    img {
      margin-left: 10px;
      max-height: 15px;
    }
  }
}

.recharge-page {
  min-height: 100%;
  padding: 50px 20px 0 20px;
  color: #000;

  .balance-wrap {
    width: 315px;
    margin: 0 auto;
    height: 144px;
    background: linear-gradient(
      90deg,
      rgba(107, 125, 252, 1) 0%,
      rgba(244, 93, 130, 1) 100%
    );
    border-radius: 5px;
    position: relative;

    .title {
      font-size: 16px;
      font-weight: 600;
      line-height: 20px;
      color: #fff;
      position: absolute;
      top: 13px;
      left: 15px;
    }
    .value-wrap {
      text-align: center;
      position: absolute;
      left: 50%;
      bottom: 32px;
      transform: translateX(-50%);
      .value {
        font-weight: bold;
        font-size: 36px;
        line-height: 43px;
        color: #fff;
      }
      .tips {
        font-size: 12px;
        color: #fff;
        line-height: 20px;
      }
    }
  }
  .title-tips {
    margin-top: 16px;
    font-size: 14px;
    color: #b5b5b5;
    line-height: 20px;
    padding: 16px 0;
  }
  .recharge-list {

    & + .recharge-list {
      margin-top: 38px;
    }
    .list-title {
      font-size: 14px;
      line-height: 20px;
    }
    .list-items {
      .item {
        display: flex;
        align-items: center;
        justify-content: space-between;
        font-size: 14px;
        font-weight: 500;
        padding: 8px 0;

        .label {
          display: flex;
          align-items: center;
          .van-icon {
            margin-right: 5px;
          }
          .hot-label {
            font-size: 16px;
            // color: rgb(266,78,110);
            color: #000;
          }
          .hot-tag {
            background-color: #dd3d2e;
            color: #FFF;
            border-radius: 5px;
            height: 18px;
            align-items: center;
            padding: 0 5px;
            font-size: 14px;
            font-weight: bold;
            margin-left: 10px;
            display: none;
          }
          .sale-money {
            color: #000;
          }
        }

        &.hot .hot-tag {
          display: inline-flex;
        }
      }
    }
  }
}
</style>
